body {
  background-color: #d6bda8;
}

.flex-container {
  display: flex;
  height: 50px;
  justify-content: center;
  background-color: #af554f;
}

.flex-container-item {
  width: 25%;
  font-size: 30px;
  text-align: center;
  padding-top: 5px;
}

.flex-container-item a {
  color: white;
  text-decoration: none;
}

.flex-container-item {
  vertical-align: middle;
}

.link.selected{
  color: #000;
  text-decoration: underline;
}

.page-content {
  margin: 1.5rem;
}

#welcome-message {
  text-align: center;
}

.form-item {
  padding: 10px;
}

.validation-form {
  padding: 25px;
}

#submit-form-button {
  width: 120px;
  height: 50px;
  font-size: 1.3rem;
}

#validation-form {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 200px;
  margin-left: auto;
  margin-right: auto;
}

#validation-form div {
  align-self: center;
  font-size: 1.6rem;
}

#validation-form input {
  vertical-align: middle;
}

.userInfo {
  font-size: 1.6rem;
}

.language-item:first-child {
  padding: 5px;
}

.disabled-link {
  color: grey;
  pointer-events: none;
}

